  .clock-numbers {
    font-size: 6rem;
  }

  .slider {
    display: none;
  }

  .secondary-screen {
    width: 100%;
    height: 160vh;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr) 0.1fr;
    grid-row-gap: 20px;
  }

  .day-one {
    grid-area: 1 / 1 / 2 / 2;
  }

  .day-two {
    grid-area: 2 / 1 / 3 / 2;
  }

  .day-three {
    grid-area: 3 / 1 / 4 / 2;
  }

  .search-bar {
    grid-area: 4 / 1 / 5 / 2;
  }
}

